@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,900;1,400&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}

body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

body {
    margin: 0;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    /* transition: background 1s ease-in-out; */
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #363636;
    border-radius: 10px;
  }
.add-location {}

.add-location input {
    height: 2rem;
    width: 15rem;
    border: .01rem solid #000;
    border-radius: .3rem;
    margin-right: 1rem;
    outline: none;
    padding: 0.5rem 1rem;
}

.add-location button {
    height: 2rem;
    width: 6rem;
    border: .01rem solid #000;
    border-radius: .3rem;
    background: #000000AA;
    color: #fff;
    font-size: .9rem;
}

.add-location button:not(:disabled):hover {
    cursor: pointer;
    background: #000;
}
.loading-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.add-location {
    position: absolute;
    right: 2rem;
    top: 2rem;
    z-index: 10;
}

.locations-list{
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1rem;
    padding: 6rem 1rem 2rem 1rem;
    overflow-y: auto;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}


.weather-pannel {        
    position: relative;
    border: 1px solid #000;
    border-radius: 1.5rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .6);
    color: #fff;
    box-shadow: 0 0 1rem #000000;
    cursor: pointer;
}

.weather-pannel .city-name {
    display: flex;
    justify-content: center;
    align-items: center;
}

.weather-pannel .info-row {
    margin-bottom: 1rem;
}

.weather-pannel .info-row.description {
    font-size: 1.3rem;
}

.weather-pannel .remove-icon {
    position: absolute;
    right: 0rem;
    top: 0rem;
    cursor: pointer;
    padding: 1rem;
}
.weather-pannel .remove-icon:hover{
    color: #999999;
}

.weather-pannel.seledted{
    border-color: aqua;
}

